import BASE_HOST from '../../util/util.js'
export default {
	data() {
		return {
			topicId: 0,
			isLoved: false,
			commentIsLoved: {},
			pageNum: 1,
			pageSize: 2,
			totalNum: 0,
			commentList: [],
			baseNum: {}
		}
	},
	onLoad(option) {
		this.topicId = option.id
	},
	onShow() {
		this.getPageComment()
		this.getTotalNum()
	},
	filters: {
		dateFormat(value) {
			var d = new Date(value)
			return d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate()
		},
		userAvatarFilter(value) {
			if (value.substr(0,5)==='https') {
				return value
			} else {
				return BASE_HOST + value
			}
		},
		commentImgFilter(value) {
			return BASE_HOST + value
		}
	},
	methods: {
		getMore() {
			if((this.pageNum) * this.pageSize >= this.baseNum.parentComment ) {
				uni.showToast({
					title: '没有更多了！',
					duration: 2000,
					icon: 'none'
				})
				return
			}
			
			this.pageNum = this.pageNum + 1
			var _this = this
			uni.request({
				url:BASE_HOST + 'comment/getPageComment',
				method:'GET',
				data:{
					pageNum: _this.pageNum,
					pageSize: _this.pageSize,
					topicId: _this.topicId
				},
				success(res) {
					var list = res.data.data.data
					list.forEach((item, index) => {
						if(item.comment_img !== '') {
							item.comment_img = item.comment_img.split(',')
						}
						item.children.forEach((item2, index2) => {
							if(item2.comment_img !== '') {
								item2.comment_img = item2.comment_img.split(',')
							}
						})
					})
					console.log('000')
					console.log(list)
					// _this.commentList.push(list)
					_this.commentList = _this.commentList.concat(list)
				}
			})
		},
		getTotalNum() {
			var _this = this
			uni.request({
				url:BASE_HOST + 'comment/getTotalOfTopic',
				method:'GET',
				data:{
					topicId: _this.topicId
				},
				success(res) {
					_this.baseNum = res.data.data
				}
			})
		},
		zan(row) {
			var _this = this
			if(this.commentIsLoved[row.id]) {
				// 已点赞 
				uni.showToast({
					title: '已点赞！',
					duration: 2000,
					icon: 'none'
				})
			} else {
				uni.request({
					url:BASE_HOST + 'comment/love',
					method:'GET',
					data:{
						id: row.id
					},
					success(res) {
						_this.$set(_this.commentIsLoved,row.id, true)
					}
				})
			}
			console.log(this.commentIsLoved)
		},
		getPageComment(){
			var _this = this
			uni.request({
				url:BASE_HOST + 'comment/getPageComment',
				method:'GET',
				data:{
					pageNum: _this.pageNum,
					pageSize: _this.pageSize,
					topicId: _this.topicId
				},
				success(res) {
					_this.commentList = res.data.data.data
					_this.commentList.forEach((item, index) => {
						if(item.comment_img !== '') {
							item.comment_img = item.comment_img.split(',')
						}
						item.children.forEach((item2, index2) => {
							if(item2.comment_img !== '') {
								item2.comment_img = item2.comment_img.split(',')
							}
						})
					})
					console.log(_this.commentList)
				}
			})
		},
		toDianzan() {
			if(this.isLoved){
				uni.showToast({
					title: '已赞过',
					duration: 2000
				})
				return
			}
			var _this = this
			uni.request({
				url: BASE_HOST + 'topic/love',
				method:'GET',
				data:{
					id: _this.topicId
				},
				success(res) {
					_this.isLoved = true
					uni.showToast({
						title: '点赞成功',
						duration: 2000
					})
				}
			})
		},
		toAddComment(com) {
			if(com === 0) {
				uni.navigateTo({
					url:'./addComment?topicId=' + this.topicId + '&commentId=' + 0
				})
			} else {
				uni.navigateTo({
					url:'./addComment?topicId=' + this.topicId + '&commentId=' + com.id
				})
			}
			
		}
	}
}
